button {
  border: 0px;
  background: transparent;
  margin: 0px;
  padding: 0px;
  outline: none; }

.buttonContainer {
  height: 44px;
  min-width: 120px;
  background: #2abadc;
  border-radius: 25px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  color: white;
  transition: box-shadow 240ms cubic-bezier(0.075, 0.82, 0.165, 1);
  box-shadow: 0px 0px 0px 0px #1e98b5; }
  .buttonContainer:hover {
    box-shadow: 0px 2px 5px 1px #1e98b5; }
  .buttonContainer:active {
    background: #21abcb; }
  .buttonContainer:disabled {
    background: #ddd;
    box-shadow: 0px 0px 0px 0px white; }
  .buttonContainer.outline {
    background: white;
    color: #2abadc;
    border: #2abadc solid 2px;
    box-shadow: 0px 0px 0px 0px white;
    font-weight: 600; }
    .buttonContainer.outline:active {
      background: #f2f2f2; }
    .buttonContainer.outline:disabled {
      border: #ddd solid 2px;
      color: #ddd; }
